.Card {
  border: 3px solid black;
  border-radius: 10px;
  background-color: white;
  display: flex;
  flex-direction: row;
  flex: 1;
  margin: 10px;
}
.foto {
  height: 150px;
  width: 150px;
}

.Valor {
  font-weight: 300;
}
.Image {
  margin: auto 25px;
  background-color: black;
  color: white;
  align-items: center;
  justify-content: space-around;
  display: flex;
  width: 150px;
  height: 150px;
}

.Descricao {
  margin: 25px;
  /* background-color: yellow; */
  color: gray;
  display: flex;
  flex-direction: column;
  flex: 1;
}
.Cima {
  /* background-color: lightblue; */
  color: gray;

  justify-content: center;
  font-size: 2rem;
  display: flex;
  flex: 1;
}

.Baixo {
  /* background-color: lightblue; */
  color: gray;
  justify-content: space-between;
  display: flex;
  margin: 0 50px;
  font-size: 2rem;
}
